<template>
    <div id="insManagement">
        <!-- 机构列表 --><div id="ins_centent">
        <span>机构名称:</span>
        <el-select v-model="customerInsName" placeholder="请选择" @change="insChoice()">
          <el-option
            v-for="item in optionsIns"
            :key="item.customerName"
            :value="item.customerName"
            :id="item.customerID">
          </el-option>
        </el-select>
        <!-- 添加新机构用户 -->
        <el-button type="primary" @click="isInsShowClick">添加新机构</el-button>
        <el-button type="danger" @click="deleteCus" size="small">删除机构</el-button>
        <div id="altAdd" v-show="isInsshow">
          <!--  -->
          <el-collapse v-model="activeInsNames">
            <el-collapse-item title="新机构信息" name="1">
              <span style="padding-top:8px">机构名称:</span>
              <el-input v-model="userInsName" placeholder="请输入新的机构名"></el-input>
              <el-button type="success" @click="saveInsClick">点击保存</el-button>
            </el-collapse-item>
          </el-collapse>
        </div></div>
    </div>
</template>

<script>
import axios from "axios";
import common from "@/utils/common";
export default {
  // 注入reload方法刷新页面
  inject:['reload'],
  name: "institutions",
  data() {
    return {
      optionsIns: [],
      customerInsName: "", //机构名
      isInsshow:false,//显示添加新机构
      activeInsNames: ['1'],
      userInsName:'',//添加的新机构名称
      customerIDs:'',//customerID
    };
  },
  mounted() {
    // 获取机构名
    this.getIns();
  },
  methods: {
    //   获取机构名
    getIns() {
      console.log(domain.accQueryCustomer)
      axios
        .get(domain.accQueryCustomer)
        .then(response => {
          console.log(response)
          if (response.data.httpCode == '200') {
            for (let j = 0; j < response.data.responseEntity.length; j++) {
              // console.log(response.data[i])
              this.optionsIns.push(response.data.responseEntity[j]);
            }
          } else {
            common.altPrompt('未找到机构名，请刷新重试')
          }
        })
        .catch(error => {
          console.log(error);
        });
      console.log(this.optionsIns);
    },
    // 选择机构名
    insChoice(){
        console.log(this.customerInsName)
    },
    // 添加新机构按钮
    isInsShowClick(){
      this.isInsshow = true;
    },
    // 点击保存新机构名
    saveInsClick(){
      console.log(this.userInsName)
      // let userInsNameReg = /^[A-Za-z0-9]+$/;
      if (common.isNull(this.userInsName)) {
        common.altPrompt('添加新机构名不能为空')
        return false
      }
      //  else if(userInsNameReg.test(this.userInsName)){
      //   console.log('机构名正确')
      // }else{
      //   common.altPrompt('机构名为英文字母、数字')
      //   return false
      // }
      console.log(domain.insAddCustomer+this.userInsName)
      axios
          .get(domain.insAddCustomer+this.userInsName)
          .then((response) => {
            console.log(response)
            if(response.data.httpCode == '500'){
              common.altPrompt('添加失败，机构已存在')
            }else if (response.data.httpCode == '200'){
              this.reload();
              common.altPrompt('添加新机构成功')
              this.userInsName = "";
              this.isInsshow = false;
            }
          })
          .catch((error) => {
            console.log(error)
          }
      )
    },
    // 点击删除机构按钮
    deleteCus(){
      console.log(this.customerInsName,this.optionsIns)
      // for循环找出机构名对应的customerID
      for (let i = 0; i < this.optionsIns.length; i++) {
        if (this.customerInsName == this.optionsIns[i].customerName) {
          console.log(this.optionsIns[i].customerID)
          this.customerIDs = this.optionsIns[i].customerID;
        }
      }
      console.log(this.customerIDs)
      if (common.isNull(this.customerInsName)) {
        common.altPrompt('请先选择机构名称')
        return false
      }
      // 删除机构前弹框
      this.$confirm('此操作将永久删除该机构,以及该机构下的用户信息, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          console.log('删除成功',domain.insDelCustomer+this.customerIDs)
          // 调删除接口
          axios
              .get(domain.insDelCustomer+this.customerIDs)
              .then((response) => {
                console.log(response)
                if (response.data.httpCode == '200') {
                  this.reload();
                    common.altPrompt('删除机构成功')
                    this.customerInsName = '';
                    return false;
                }else if(response.data.httpCode == '500'){
                  common.altPrompt('删除失败,请先删除该机构下的用户信息')
                }else if(response.data.httpCode == '400'){
                  common.altPrompt('删除失败,请先删除该机构下的账户信息')
                }
              })
              .catch((error) => {
                console.log(error)
              })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
      
    },
  }
};
</script>

<style>
</style>



