<template>
    <div id="glLeft">
      <div id="glRight">
            <el-table
                :data="tableDataUser"
                :span-method="objectSpanMethodUser"
                border
                style="width: 100%; margin-top: 20px">
                <el-table-column
                    fixed
                    label="机构名称"
                    width="180">
                    <template slot-scope="scope">
                        <!-- 机构列表 -->
                      <el-select v-model="customerName" placeholder="请选择" @change="leftChoice()">
                        <el-option
                          v-for="item in optionsLeft"
                          :key="item.customerName"
                          :value="item.customerName"
                          :id="item.customerID">
                        </el-option>
                      </el-select>
                      <!-- 添加新机构用户 -->
                      <el-button type="success" @click="addNewUser">添加新用户</el-button>
                    </template>
                </el-table-column>
                <el-table-column  prop="phone"  label="手机号码"></el-table-column>
                <el-table-column  prop="role"  label="角色"></el-table-column>
                <el-table-column  prop="username"  label="用户名"></el-table-column>
                <el-table-column fixed="right" width="100"  label="操作">
                  <template slot-scope="scope">
                    <el-button type="danger" size="small" @click="delCustomerUser(scope.row, tableDataUser)">删除用户</el-button>
                  </template>
                </el-table-column>
            </el-table>
        </div>
        <!-- 弹出框 -->
        <div id="addAltDiv" v-show="isShowAddDiv">
          <div id="altContent">
            <i class="el-icon-close" @click="cancelX"></i>
            <div>
              <span>用户登录名</span>
              <el-input v-model="userLogName" placeholder="请输入用户登录名"></el-input>
            </div>
            <div>
              <span>用户登录密码</span>
              <el-input type="password" v-model="userLogPsw" placeholder="请输入用户登录密码"></el-input>
            </div>
            <div>
              <span>用户手机号码</span>
              <el-input v-model="userLogPhone" placeholder="请输入用户手机号码" maxlength="11"></el-input>
            </div>
            <div>
              <!-- 角色 -->
              <p>角色</p>
              <el-select v-model="rolesTCA" placeholder="请选择" @change="rolesChoice()">
                <el-option
                  v-for="item in roles"
                  :key="item.role"
                  :value="item.role"
                  :id="item.id">
                </el-option>
              </el-select>
            </div>
            <div>
              <el-button round @click="cancelUserIm">取消</el-button>
              <el-button type="success" round @click="subUserIm">提交</el-button>
            </div>
          </div>
        </div>
    </div>
</template>

<script>
import axios from "axios";
import common from "@/utils/common";
export default {
  // 注入reload方法刷新页面
  inject:['reload'],
  name: "userInformation",
  data(){
    return {
      tableDataUser: [{password:'',phone:'',role:'',username:'',userid:''}],//用户信息等
      tableDataUserID:'',//根据机构名获取userid
      optionsLeft:[],
      customerName:'',//机构名
      customerIDs:'',//机构名对应customerid
      userIDS:'',//机构名对应userid
      IDs:'',//机构名对应iD
      activeNames: [],
      isShowAddDiv:false,//是否显示弹出框
      userLogName:'',//用户登录名
      userLogPsw:'',//用户登录密码
      userLogPhone:'',//用户手机号码
      rolesTCA:'',
      roles:[
        {
          id:'001',
          role:'trader',
        },
        {
          id:'003',
          role:'admin',
        },
      ],
    }
  },
  methods:{
    // 获取机构名
    getQueryCustomer() {
      console.log(domain.accQueryCustomer)
      axios
          .get(domain.accQueryCustomer)
          .then((response) => {
            console.log(response)
            if (response.data.httpCode == '200') {
              for (let i = 0; i < response.data.responseEntity.length; i++) {
                // console.log(response.data[i])
                this.optionsLeft.push(response.data.responseEntity[i])
              }
            } else {
              common.altPrompt('未找到机构名，请刷新重试')
            }
          })
          .catch((error) => {
            console.log(error)
          })
      console.log(this.optionsLeft)
    },
    // 根据机构名显示具体信息
    leftChoice(){
      console.log(this.customerName)
      // for循环找出机构名对应的customerID
      for (let i = 0; i < this.optionsLeft.length; i++) {
        if (this.customerName == this.optionsLeft[i].customerName) {
          console.log(this.optionsLeft[i].customerID)
          this.customerIDs = this.optionsLeft[i].customerID;
        }
      }
      console.log(this.customerIDs,domain.useGetUser+this.customerIDs)
      // 提供变量接收账户和电话的信息
      let getTabDaUser = [];
      let tableDataUserPhone = [];
      axios
          .get(domain.useGetUser+this.customerIDs)
          .then((response) => {
            console.log(response)
            if (response.data.httpCode == '200') {
              if (response.data.responseEntity.length == 0) {
                console.log('......')
                getTabDaUser = [{password:'',phone:'',role:'',username:'',userid:''}];
              }else{
                getTabDaUser = response.data.responseEntity;
              }
            } else if (response.data.httpCode == '500'){
              common.altPrompt('查询数据库出错,未发现用户信息')
              getTabDaUser = [{password:'',phone:'',role:'',username:'',userid:''}];
            }
            
          })
          .catch((error) => {
            console.log(error)
          })
      // 获取电话
      setTimeout(function(){
        // for循环找出机构名对应的customerID
        for (let j = 0; j < this.optionsLeft.length; j++) {
          if (this.customerName == this.optionsLeft[j].customerName) {
            console.log(this.optionsLeft[j].customerID)
            this.customerIDs = this.optionsLeft[j].customerID;
          }
        } 
        console.log(domain.useGetphone+this.customerIDs)
        axios
          .get(domain.useGetphone+this.customerIDs)
          .then((response) => {
            console.log(response)
            if (response.data.httpCode == '200') {
              tableDataUserPhone = response.data.responseEntity;
              if (getTabDaUser.length == 0) {
                this.tableDataUser = getTabDaUser;
                return false
              } else {
                for (let l = 0; l < getTabDaUser.length; l++) {
                  for (let k = 0; k < tableDataUserPhone.length; k++) {
                    if(getTabDaUser[l].userid == tableDataUserPhone[k].userId){
                      getTabDaUser[l].phone = tableDataUserPhone[k].phone
                    }
                  }
                }
                console.log(getTabDaUser)
                this.tableDataUser = getTabDaUser;
              }
            } else if (response.data.httpCode == '500'){
              common.altPrompt('查询数据库出错,未发现电话号码信息')
              this.tableDataUser = [{password:'',phone:'',role:'',username:'',userid:''}];
            }
            
          })
          .catch((error) => {
            console.log(error)
          })
      }.bind(this), 1000);
    },
    // 
    // handleChange(val) {
    //   console.log(val);
    // },
    // 点击添加新用户按钮
    addNewUser(){
      console.log(this.customerName)
      if (common.isNull(this.customerName)) {
        common.altPrompt('请先选择机构名称')
        return false
      } else {
        this.isShowAddDiv = true;
      }
      // for循环找出机构名对应的customerID
      for (let i = 0; i < this.optionsLeft.length; i++) {
        if (this.customerName == this.optionsLeft[i].customerName) {
          console.log(this.optionsLeft[i].customerID)
          this.customerIDs = this.optionsLeft[i].customerID;
        }
      }
      console.log(this.customerIDs)
    },
    // 选择角色
    rolesChoice(){
      console.log(this.rolesTCA)
    },
    // 提交添加新用户信息
    subUserIm(){
      console.log(this.customerName,'---'+this.customerIDs,'+++'+this.userLogID,this.userLogName,this.userLogPsw,this.userLogPhone,this.rolesTCA)
      // 用户登录名，密码格式验证
      if(common.isNull(this.userLogName)){
        common.altPrompt('用户名不能为空')
        return false
      }
      if(common.isNull(this.userLogPsw)){
        common.altPrompt('用户登录密码不能为空')
        return false
      }
      // 手机号格式验证
      let userLogPhoReg = /^((13[0-9])|(14[5|7])|(17[5|7|6])|(15([0-3]|[5-9]))|(18[0,5-9]))\d{8}$/;
      if (common.isNull(this.userLogPhone)) {
        common.altPrompt("手机号不能为空");
        return false;
      } else if (userLogPhoReg.test(this.userLogPhone)) {
        console.log('用户手机号格式正确')
      } else {
        common.altPrompt("手机号错误");
        return false;
      }
      // 角色选择验证
      if (common.isNull(this.rolesTCA)) {
        common.altPrompt('请选择一个角色')
        return false
      }

      console.log(domain.useAddUser)
      axios
      .post(domain.useAddUser,
      {
        customerId:this.customerIDs,
        userid:this.userLogID,
        username:this.userLogName,
        password:this.userLogPsw,
        role:this.rolesTCA,
        phone:this.userLogPhone
      })
        .then((response) => {
          console.log(response)
          if (response.data.httpCode == '200') {
            //this.reload();//调用刷新方法
            this.userLogName = '';
            this.userLogPsw = '';
            this.userLogPhone = '';
            this.rolesTCA = '';
            common.altPrompt('添加成功')
            this.isshow = false;
            this.isShowAddDiv = false;
            this.leftChoice()
          } else{
            this.userLogName = '';
            this.userLogPsw = '';
            this.userLogPhone = '';
            this.rolesTCA = '';
            common.altPrompt('用户名已存在，请重新添加')
            this.isShowAddDiv = false;
          }
        })
        .catch((error) => {
          console.log(error)
        })
    },
    // 取消添加新用户信息
    cancelUserIm(){
      this.userLogName = '';
      this.userLogPsw = '';
      this.userLogPhone = '';
      this.rolesTCA = '';
      common.altPrompt('取消成功')
      this.isshow = false;
      this.isShowAddDiv = false;
    },
    // X号取消
    cancelX(){
      this.userLogName = '';
      this.userLogPsw = '';
      this.userLogPhone = '';
      this.rolesTCA = '';
      common.altPrompt('取消成功')
      this.isshow = false;
      this.isShowAddDiv = false;
    },
    // 删除机构对应用户
    delCustomerUser(row,index){
      console.log(this.customerName,row,index)
      if (common.isNull(this.customerName)) {
        common.altPrompt('请先选择机构名称')
        return false
      }
      // 删除机构名下对应的用户，传参userid
      console.log(row.userid,domain.useDelCustomerUser+row.userid)
      this.$confirm('此操作将永久删除该机构下的用户，以及该用户信息, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          console.log(domain.insDelCustomer+this.insIDs)
          // 调删除接口
          axios
          .get(domain.useDelCustomerUser+row.userid)
          .then((response) => {
            console.log(response)
            if (response.data.httpCode == '200') {
              common.altPrompt('删除用户成功')
              this.leftChoice()
            }else if (response.data.httpCode == '500'){
              common.altPrompt('请先删除账户')
              return false
            }
          })
          .catch((error) => {
            console.log(error)
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
    },
    // 合并状态栏
    objectSpanMethodUser({ row, column, rowIndex, columnIndex }) {
      // console.log('当前行:'+row,'当前列:'+column,'当前行号:'+rowIndex,'当前列号:'+columnIndex)
      if (columnIndex === 0) {
        if (rowIndex % 9999 === 0) {
          return {
            rowspan: 9999,//单元格可横跨的行数
            colspan: 1,//单元格可横跨的列数
          };
        } else {
          return {
            rowspan: 0,
            colspan: 0
          };
        }
      }
    },
  },
  mounted:function(){
    // 获取机构名
    this.getQueryCustomer()
  }
};
</script>

<style>
</style>



