<template>
    <div id="tabsSeeChoice">
        <ul class="tabs">
            <li class="li-tab" v-for="(item,index) in tabsParam" 
            @click="toggleTabs(index)"
            :class="{active:index!=nowIndex}" :key="item">{{item}}</li>
        </ul>
        <span @click="loginAginSee" class="tabsSeeChoice">退出系统</span>
        <!-- 机构 -->
        <div class="divTab" v-show="nowIndex===0">
            <institutions></institutions>
        </div>
        <!-- 用户 -->
        <div class="divTab" v-show="nowIndex===1">
            <userInformation></userInformation>
        </div>
        <!-- 账户 -->
        <div class="divTab" v-show="nowIndex===2">
            <accountInformation></accountInformation>
        </div>
    </div>
</template>

<script>
import institutions from "@/page/institutions/institutions";
import userInformation from "@/page/userInformation/userInformation";
import accountInformation from "@/page/accountInformation/accountInformation";
export default {
  name: "tabsEntrust",
  data() {
    return {
      tabsParam: ["机构信息管理","用户信息管理", "账户信息管理"], //（这个也可以用对象key，value来实现）
      nowIndex: 0 //默认第一个tab为激活状态
    };
  },
  components: {
    institutions,
    userInformation,
    accountInformation,
  },
  methods: {
    toggleTabs: function(index) {
      this.nowIndex = index;
    },
    // 跳转首页
    loginAginSee(){
      this.$router.push({
        name:'login',
        param:''
      })
    },
  }
};
</script>

<style>
.active {
  background: #eee;
}
.tabs {
  /* width: 200px;
        height: 20px; */
  font: 0;
  padding: 0;
}
.li-tab {
  width: 33%;
  height: 100%;
  display: inline-block;
  text-align: center;
}
/* .divTab {
  width: 200px;height: 300px;
} */
</style>



