<template>
    <div>
        <!--  -->
        <div id="glRight">
            <el-table
                :data="tableData"
                :span-method="objectSpanMethod"
                border
                style="width: 100%; margin-top: 20px">
                <el-table-column
                    fixed
                    label="机构名称"
                    width="180">
                    <template slot-scope="scope">
                        <el-select v-model="valueMaName" placeholder="请选择" @change="handleClick()">
                            <el-option
                            v-for="item in options"
                            :key="item.customerName"
                            :value="item.customerName"
                            :id="item.customerID">
                            </el-option>
                        </el-select>
                        <el-button @click="seeClick(scope.row)" type="success" size="small">添加新账户</el-button>
                    </template>
                </el-table-column>
                <el-table-column  prop="clientName"  label="母账户名称"></el-table-column>
                <el-table-column  prop="broker"  label="券商名称"></el-table-column>
                <el-table-column  prop="adaptorType"  label="柜台适配器类型"></el-table-column>
                <el-table-column  prop="adaptorUser1"  label="柜台用户名1"></el-table-column>
                <el-table-column  prop="adaptorUser2"  label="柜台用户名2"></el-table-column>
                <el-table-column  prop="adaptorUser3"  label="柜台用户名3"></el-table-column>
                <el-table-column  prop="isCredit"  label="是否信用账户"></el-table-column>
                <el-table-column fixed="right" width="100"  label="操作">
                  <template slot-scope="scope">
                    <el-button type="danger" size="small" @click="delClient(scope.row)">删除账户</el-button>
                  </template>
                </el-table-column>
            </el-table>
        </div>
        <!-- 弹出框 -->
        <div id="addAltMaDiv" v-show="isMaShow">
          <div id="altMaDiv">
            <div>
              <span>母账户名称</span>
              <el-input v-model="userMaName" placeholder="请输入母账户名称"></el-input>
            </div>
            <div>
              <span>柜台用户名1</span>
              <el-input v-model="userCounterName1" placeholder="请输入柜台用户名1"></el-input>
            </div>
            <div>
              <span>柜台的密码1</span>
              <el-input type="password" v-model="userCounterPsw1" placeholder="请输入柜台的密码1"></el-input>
            </div>
            <div>
              <span>柜台用户名2</span>
              <el-input v-model="userCounterName2" placeholder="请输入柜台用户名2"></el-input>
            </div>
            <div>
              <span>柜台的密码2</span>
              <el-input type="password" v-model="userCounterPsw2" placeholder="请输入柜台的密码2"></el-input>
            </div>
            <div>
              <span>柜台用户名3</span>
              <el-input v-model="userCounterName3" placeholder="请输入柜台用户名3"></el-input>
            </div>
            <div>
              <span>柜台的密码3</span>
              <el-input type="password" v-model="userCounterPsw3" placeholder="请输入柜台的密码3"></el-input>
            </div>
            <div class="lab-buy">
                <span>是否信用账户</span>
                <el-radio v-model="creditAccounts" label="是">是</el-radio>
                <el-radio v-model="creditAccounts" label="否">否</el-radio>
            </div>
            <div>
              <el-button round @click="cancelMaClick">取消</el-button>
              <el-button type="success" round @click="subMaClick">点击保存</el-button>
            </div>
          </div>
        </div>
    </div>
</template>

<script>
import axios from "axios";
import common from "@/utils/common";
export default {
  // 注入reload方法刷新页面
  inject:['reload'],
  name: "accountInformation",
  data() {
    return {
      tableData: [{clientName:'',broker:'',adaptorType:'',adaptorUser1:'',adaptorPwd1:'',adaptorUser2:'',adaptorPwd2:'',adaptorUser3:'',adaptorPwd3:''}],//账户信息，包括产品名等
      options: [],//机构名
      valueMaName: "",//下拉框选择的客户名
      valueMaID:'',//机构名对应customerID
      userMaName:'',//母账户名称
      userMaClientName:'',//机构名下对应母账户名称clientName
      userMaNameID:'',//机构名下母账户对应id
      userCounterName1:'',//柜台名
      userCounterName2:'',
      userCounterName3:'',
      userCounterPsw1:'',//柜台密码
      userCounterPsw2:'',
      userCounterPsw3:'',
      creditAccounts:'',//是否信用账户
      isMaShow:false,//是否显示弹出框
    };
  },
  mounted: function() {
    // 获取机构名
    this.getdatas();
  },
  methods: {
    // 获取机构名
    getdatas() {
      console.log(domain.accQueryCustomer)
      axios
          .get(domain.accQueryCustomer)
          .then((response) => {
            console.log(response)
            if (response.data.httpCode == '200') {
              for (let k = 0; k < response.data.responseEntity.length; k++) {
                // console.log(response.data[k])
                this.options.push(response.data.responseEntity[k])
              }
            } else {
              common.altPrompt('未找到机构名，请刷新重试')
            }
          })
          .catch((error) => {
            console.log(error)
          }
      )
    },
    // 根据机构名显示账户
    handleClick() {
      console.log(this.valueMaName);
      // for循环找出机构名对应的customerID
      for (let i = 0; i < this.options.length; i++) {
        if (this.valueMaName == this.options[i].customerName) {
          console.log(this.options[i].customerID)
          this.valueMaID = this.options[i].customerID;
        }
      }
      console.log(this.valueMaID,domain.accGetCustomer+this.valueMaID)
      axios
          .get(domain.accGetCustomer+this.valueMaID)
          .then((response) => {
            console.log(response)
            if (response.data.httpCode == '200') {
              if (response.data.responseEntity.length == '0') {
                console.log('......')
                this.tableData = [{clientName:'',broker:'',adaptorType:'',adaptorUser1:'',adaptorPwd1:''}];
              }else{
                for (let j = 0; j < response.data.responseEntity.length; j++) {
                  if (response.data.responseEntity[j].isCredit == '0') {
                    response.data.responseEntity[j].isCredit = '否'
                  }else if (response.data.responseEntity[j].isCredit == '1') {
                    response.data.responseEntity[j].isCredit = '是'
                  }
                }
                this.tableData = response.data.responseEntity;
              }
            } else if (response.data.httpCode == '500'){
              common.altPrompt('查询数据库出错，未发现账户信息')
              this.tableData = [{clientName:'',broker:'',adaptorType:'',adaptorUser1:'',adaptorPwd1:''}];
            }
          })
          .catch((error) => {
            console.log(error)
          })
          console.log(this.tableData)
    },
    // 点击添加新账户
    seeClick(row) {
      console.log(row,this.valueMaName);
      if (common.isNull(this.valueMaName)) {
        common.altPrompt('请先选择机构名称')
        return false
      } else {
        this.isMaShow = true;
      }
    },
    // 合并状态栏
    objectSpanMethod({ row, column, rowIndex, columnIndex }) {
      // console.log('当前行:'+row,'当前列:'+column,'当前行号:'+rowIndex,'当前列号:'+columnIndex)
      if (columnIndex === 0) {
        if (rowIndex % 9999 === 0) {
          return {
            rowspan: 9999,//单元格可横跨的行数
            colspan: 1,//单元格可横跨的列数
          };
        } else {
          return {
            rowspan: 0,
            colspan: 0
          };
        }
      }
    },
    // 保存添加的账户信息
    subMaClick(){
      console.log(this.valueMaName,this.valueMaID,this.userMaName,this.userCounterName1,this.userCounterName2,this.userCounterName3,this.userCounterPsw1,this.userCounterPsw2,this.userCounterPsw3,this.creditAccounts)
      // for循环找出机构名对应的customerID
      for (let i = 0; i < this.options.length; i++) {
        if (this.valueMaName == this.options[i].customerName) {
          console.log(this.options[i].customerID)
          this.valueMaID = this.options[i].customerID;
        }
      }

      if (common.isNull(this.userMaName)) {
        common.altPrompt('母账户名称不能为空')
        return false
      }
      if (
        // 只有一个用户名和密码完成，另外两个密码是空
          (
            (common.isNotNull(this.userCounterName1) && common.isNotNull(this.userCounterPsw1) && common.isNull(this.userCounterPsw2) && common.isNull(this.userCounterPsw3)) || 
            (common.isNotNull(this.userCounterName2) && common.isNotNull(this.userCounterPsw2) && common.isNull(this.userCounterPsw1) && common.isNull(this.userCounterPsw3)) || 
            (common.isNotNull(this.userCounterName3) && common.isNotNull(this.userCounterPsw3) && common.isNull(this.userCounterPsw1) && common.isNull(this.userCounterPsw2))
          )
          // 只有2个用户名和密码完整，另外1个密码是空
          ||
          (
            (
              (common.isNotNull(this.userCounterName1) && common.isNotNull(this.userCounterPsw1)) &&
              (common.isNotNull(this.userCounterName2) && common.isNotNull(this.userCounterPsw2)) &&
              common.isNull(this.userCounterPsw3)
            )
            ||
            (
              (common.isNotNull(this.userCounterName1) && common.isNotNull(this.userCounterPsw1)) &&
              (common.isNotNull(this.userCounterName3) && common.isNotNull(this.userCounterPsw3)) &&
              common.isNull(this.userCounterPsw2)
            )
            ||
            (
              (common.isNotNull(this.userCounterName2) && common.isNotNull(this.userCounterPsw2)) &&
              (common.isNotNull(this.userCounterName3) && common.isNotNull(this.userCounterPsw3)) &&
              common.isNull(this.userCounterPsw1)
            )
          )
          // 3个用户名和密码都完整
          ||
          (
            (common.isNotNull(this.userCounterName1) && common.isNotNull(this.userCounterPsw1)) &&
            (common.isNotNull(this.userCounterName2) && common.isNotNull(this.userCounterPsw2)) &&
            (common.isNotNull(this.userCounterName3) && common.isNotNull(this.userCounterPsw3))
          )
        ) {
        console.log('添加柜台用户名和密码正确')
      }else{
        common.altPrompt('请添加完整的柜台用户名和密码')
        return false
      }
      // 是否信用账户
      if (common.isNull(this.creditAccounts)) {
        common.altPrompt('请选择是否信用账户')
        return false;
      }else if (this.creditAccounts == '是') {
        this.creditAccounts = '1'
      } else if (this.creditAccounts == '否'){
        this.creditAccounts = '0'
      }
      console.log(this.creditAccounts)
      console.log(this.valueMaID,domain.accAddClient)
      axios
      .post(domain.accAddClient,
      {
        CustomerID:this.valueMaID,
        ClientName:this.userMaName,
        AdaptorUser1:this.userCounterName1,
        AdaptorUser2:this.userCounterName2,
        AdaptorUser3:this.userCounterName3,
        AdaptorPwd1:this.userCounterPsw1,
        AdaptorPwd2:this.userCounterPsw2,
        AdaptorPwd3:this.userCounterPsw3,
        IsCredit:this.creditAccounts,
      })
          .then((response) => {
            // 表里有500，添加成功200
            console.log(response)
            if (response.data.httpCode == '200') {
              // this.reload();
              this.userMaName='';
              this.userCounterName1='';
              this.userCounterName2='';
              this.userCounterName3='';
              this.userCounterPsw1='';
              this.userCounterPsw2='';
              this.userCounterPsw3='';
              this.creditAccounts='';
              common.altPrompt('添加账户成功')
              this.isMaShow = false;
              this.handleClick();
            } else{
              this.userMaName='';
              this.userCounterName1='';
              this.userCounterName2='';
              this.userCounterName3='';
              this.userCounterPsw1='';
              this.userCounterPsw2='';
              this.userCounterPsw3='';
              this.creditAccounts='';
              common.altPrompt('账户已存在，请重新添加')
              this.isMaShow = false;
            }
          })
          .catch((error) => {
            console.log(error)
          })
    },
    // 取消添加母账户信息
    cancelMaClick(){
      this.userMaName='';
      this.userCounterName1='';
      this.userCounterName2='';
      this.userCounterName3='';
      this.userCounterPsw1='';
      this.userCounterPsw2='';
      this.userCounterPsw3='';
      common.altPrompt('已取消添加母账户')
      this.isMaShow = false;
    },
    // 删除账户
    delClient(row){
      console.log(this.valueMaName,this.tableData,row,row.clientName);
      if (common.isNull(this.valueMaName)) {
        common.altPrompt('请先选择机构名称')
        return false
      }
      // customerID
      console.log(this.userMaNameID,domain.accDelClient+this.userMaNameID)
      this.$confirm('此操作将永久删除该机构下的账户，以及该账户信息, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          console.log('111删除成功',domain.insDelCustomer+row.customerID)
          // 调删除接口
          axios
          .get(domain.accDelClient+row.customerID)
          .then((response) => {
            console.log(response)
            if (response.data.httpCode == '200') {
              // this.reload();
              common.altPrompt('删除账户成功')
              this.handleClick();
            }else{
              common.altPrompt('删除失败，请重试')
            }
          })
          .catch((error) =>{
            console.log(error)
          })
        }).catch(() => {
          console.log('222取消删除')
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
      
    }
  }
};
</script>

<style>
</style>



